import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: '#43a047',
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

function createData(title, myData) {
  return { title, myData };
}

const rows = [
  createData('Kalorie', ''),
  createData('Proteiny', ''),
  createData('Sacharidy', ''),
  createData('Cukry', ''),
  createData('Celkové Tuky', ''),
  createData('Nasycené Mastné Kyseliny', ''),
  createData('Trans Mastné Kyseliny', ''),
  createData('Polynenasycené', ''),
  createData('Cholesterol', ''),
  createData('Vláknina', ''),
  createData('Sůl', ''),
  createData('Vápník', ''),
  createData('PHE', ''),
];

const useStyles = makeStyles({
  table: {
    maxWidth: 389,
  },
  tableRow: {
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
  },
  tableColumn: {
    flex: 1,
  },
  upperCase: {
    fontWeight: 'bold',
    textTransform: 'uppercase',
  },
  paddingVertical: {
    marginTop: '2rem',
    marginBottom: '2rem',
  },
});

export const NutritionTable = () => {
  const classes = useStyles();

  return (
    <TableContainer className={classes.paddingVertical} component={Paper}>
      <Table className={classes.table} aria-label='customized table'>
        <TableHead>
          <TableRow>
            <StyledTableCell className={classes.upperCase} align='left'>
              Moje Živiny
            </StyledTableCell>
            <StyledTableCell align='right'></StyledTableCell>
            <StyledTableCell align='right'></StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <StyledTableRow key={row.name}>
              <StyledTableCell component='th' scope='row'>
                {row.title}
              </StyledTableCell>
              <StyledTableCell align='right'></StyledTableCell>
              <StyledTableCell align='right'></StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
